body {
  margin: 0;
  font-family: 'Segoe UI', sans-serif;
  background-color: #f9f9f9;
}

.App {
  text-align: center;
}

.app-header {
  background-color: #4CAF50;
  padding: 1.5rem 2rem;
  color: white;
  position: relative;
}

.nav-and-auth {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
  margin-top: 1rem;
}

.nav-links {
  list-style: none;
  padding: 0;
  display: flex;
  gap: 1.5rem;
  margin: 0;
}

.nav-links a {
  color: white;
  text-decoration: none;
  font-weight: bold;
  font-size: 1.1rem;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #dfffe6;
}

.auth-buttons {
  display: flex;
  gap: 0.8rem;
}

.auth-buttons button {
  background-color: white;
  color: #4CAF50;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.3s ease;
}

.auth-buttons button:hover {
  background-color: #388e3c;
  color: white;
}

main {
  padding: 3rem;
}
